.App {
  text-align: center;
}

.Welcome-header {
  text-align: center;
  background-color: #f6f7f8;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.Font {
  font-size: calc(10px + 1vmin);
  color: #2e3e48;
}

.App-header {
  background-color: #823400;
  background-image: url("https://www.transparenttextures.com/patterns/wood-pattern.png");
  top: 0px;
  height: 30vmin;
  min-height: 50px;
  max-height: 200px;  
  text-align: center;
}

.Zba-logo {
  height: 30vmin;
  min-height: 50px;
  max-height: 200px;
  pointer-events: none;
}

.App-out {
  animation: fade-out 500ms linear;
}

.App-in {
  animation: fade-in 500ms linear;
}

.Div {
  margin-left: 10vmin;
  margin-top: 10vmin;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes fade-out {
  from { opacity: 1;}
  to   { opacity: 0;}
}

@keyframes fade-in {
  from { opacity: 0;}
  to   { opacity: 1;}
}

